
.play {
  cursor: pointer;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  width: 0;
  height: 0;
  border-top: 30px solid transparent;
  border-bottom: 30px solid transparent;
  border-left: 36px solid #fff;
  margin: auto;
  z-index: 1;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  left: 6px;
}
.play:before {
    content: '';
    position: absolute;
    top: -45px;
    left: -69px;
    bottom: -45px;
    right: -21px;
    border-radius: 50%;
    border: 6px solid #fff;
    z-index: 2;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
}
.play:after {
    content: '';
    opacity: 0;
    transition: opacity 0.6s;
    -webkit-transition: opacity 0.6s;
    -moz-transition: opacity 0.6s;
}
.play:hover:before, .play:focus:before {
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
}
.play.active {
    border-color: transparent;
}
.play.active:after {
      content: '';
      opacity: 1;
      width: 30px;
      height: 48px;
      background: #fff;
      position: absolute;
      right: 3px;
      top: -24px;
      border-left: 12px solid #fff;
      -webkit-box-shadow: inset 18px 0 0 0 #f9f9f9;
              box-shadow: inset 18px 0 0 0 #f9f9f9;
}
body {
  font-family: 'Titillium Web', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #010101;
  color: #f9f9f9;
  margin: 0;
  border-top: 3px solid #00BFFF;
}
.container {
  max-width: 80%;
  margin: 60px auto;
}
a {
  color: #00BFFF;
}
h1 {
  font-size: 2.5rem;
  font-weight: 300;
  color: #00BFFF;
  margin-bottom: 40px;
}
h2 {
  margin-top: 0;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.text-center {
  text-align: center;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  max-width: 1200px;
}
@media (max-width: 767px) {
.grid {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
}
}
.card {
  position: relative;
  width: 40%;
  -webkit-transition: all .8s;
  transition: all .8s;
}
@media (max-width: 767px) {
.card {
      width: 100%;
}
}
.card h2 {
    min-height: 50px;
}
.card img {
    width: 100%;
}
.card .wrapper {
    position: relative;
}
.playlist {
  width: 20%;
}
.playlist img {
    max-width: 240px;
    width: 100%;
}
@media (max-width: 767px) {
.playlist img {
        max-width: 100%;
}
}
@media (max-width: 767px) {
.playlist {
      width: 100%;
}
}
.progress-bar {
  width: 0;
  height: 4px;
  background-color: #00BFFF;
  -webkit-transition: width 1s;
  transition: width 1s;
}



